.signin{
  width: 280px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);  
  color: #4caf50;
}
.signin h1{
  float: left;
  font-size: 40px;
  border-bottom: 6px solid #4caf50;
  margin-bottom: 50px;
  padding: 13px 0;
  color: #4caf50;
}
.textbox{
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
  border-bottom: 1px solid #4caf50;
}

.textbox input{
  border: none;
  outline: none;
  background: none;
  color: black;
  font-size: 18px;
  width: 80%;
  float: left;
  margin: 0 10px;
}
.btn{
  width: 100%;
  background: none;
  border: 2px solid #4caf50;
  color:  #4caf50;
  padding: 5px;
  font-size: 18px;
  cursor: pointer;
  margin: 12px 0;

}
.btn:hover{
  transition: all .2s ease-in-out;
  transform: scale(1.01);
  color: white;
  background-color: #4caf50;
}


